html {
    scroll-behavior: smooth;
}


body.modal-open {
    overflow: hidden;
}

.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
}

.container {
    max-width: 100%;
    padding-left: 20px;
        padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;

    @media screen and (min-width: 360px) {
    width: 360px;
    }
    // @media screen and (min-width: 768px) {
    // width: 768px;
    // }
    
    @media screen and (min-width: 1920px) {
    width: 1920px;
    padding-left: 80px;
    padding-right: 80px;
}
}
     

.main-container {
    padding-top: 200px
}