}

.main-container {
  padding-top: 200px;
}

:root {
  --primary-text: #121313;
  --secondary-text: #fffefc;
  --accent-text: #78807f;
  --red-icon: #f42808;
  --primary-bg: #fffefc;
  --secondary-bg: #f4e1a9;
  --blue-bg: #9bcdca;
  --pink-bg: #efbcb7;
  --black-bg: #121313;
  --red-bg: #f42808;
}

.btn {
  width: 100%;
  max-width: 320px;
  height: 60px;
  border: 2px solid var(--black-bg);
  letter-spacing: 1.75px;
  text-transform: uppercase;
  font-family: Segoe Print;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.btn:hover, .btn:focus {
  background-color: var(--accent-text);
  color: var(--secondary-text);
}

