.backdrop {
  width: 100%;
  height: 100%;
  background-color: #9bcdca4d;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.is-hidden .modal-window {
  transform: scale(.1);
}

.modal__window {
  transform: translate(-50%, -50%) rotate(0) scale(1);
  width: 100%;
  min-height: 581px;
  background-image: url("Group 1140.e28491af.jpg");
  background-repeat: no-repeat;
  background-size: 340px 580px;
  border-radius: 4px;
  padding: 39px;
  transition: transform .25s;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

@media screen and (min-width: 360px) {
  .modal__window {
    width: 340px;
  }
}

@media screen and (min-width: 1920px) {
  .modal__window {
    width: 530px;
    background-size: 530px 580px;
  }
}

.close {
  width: 30px;
  height: 30px;
  border: 2px solid var(--primary-text);
  cursor: pointer;
  stroke: var(--primary-text);
  fill: var(--primary-text);
  background-color: #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition-property: stroke, fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.close:hover, .close:focus {
  fill: var(--accent-text);
  stroke: var(--accent-text);
  border-color: var(--accent-text);
}

.modal__title {
  text-align: center;
  color: #000;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.modal__field {
  margin-bottom: 10px;
}

.coment__field {
  margin-bottom: 20px;
}

.modal__label {
  letter-spacing: .01em;
  color: var(--primary-text);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
  display: block;
}

.modal-input-wrap {
  position: relative;
}

.modal__input {
  width: 100%;
  height: 40px;
  background-color: #0000;
  border: 1px solid #21212133;
  border-radius: 4px;
  outline: none;
  padding-left: 42px;
  transition-property: border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.modal__input:focus + .input-icon, .modal__input:hover + .input-icon, .modal__input:focus, .modal__input:hover {
  stroke: var(--accent-text);
  border-color: var(--black-bg);
  background-color: var(--primary-bg);
}

.input-icon {
  fill: var(--accent-text);
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.modal__coment {
  width: 100%;
  height: 120px;
  letter-spacing: .01em;
  resize: none;
  background-color: #0000;
  border: 1px solid #21212133;
  border-radius: 4px;
  outline: none;
  padding: 12px 15px;
  font-size: 14px;
  line-height: 1.14;
  transition-property: border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.modal__coment:focus, .modal__coment:hover {
  border-color: var(--black-bg);
  background-color: var(--primary-bg);
}

.modal__coment::placeholder {
  letter-spacing: .01em;
  color: #75757580;
  font-size: 14px;
  line-height: 1.14;
}

