h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;

}

a {
    text-decoration: none;
    color: inherit;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

button {
    background: none;
    border: none;
    cursor: pointer;
}

svg{
    fill: none;
    
}